.tools{
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content: center;
    height: 100vh;
    width: 100%;
    margin-left: -100px;
}
.skilltop,.tooltop,.codetop{
    transform: rotate(270deg);
    position: absolute;
    margin-left: -200px;
}
.tooltop{
    margin-left: -250px;
}
.secele{
    display: grid;
    grid-template-columns: repeat(2,auto);
    justify-content: left;
    align-items: center;
}
.skicon{
    color: white;
    font-size: 50px;
    margin-right: 40px;
    padding-top: 20px;
}
.skillele{
    gap: 30px;
    width: 40vw;
    background-color: #2a364550;
    padding: 10px;
    border-radius: 10px;
    border: 1px solid grey;
}
.skillsec{
    display:flex;
    grid-template-columns: repeat(2,auto);
    justify-content: center;
}
.toolhead{
    position: absolute;
    right: 0;
    left: auto;
    margin-top: 30vh;
    align-items: center;
    justify-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 80px;
    color: white;
    margin-right: 10vw;
}
.skillheadp{
    font-size: 18px;
    margin-top:-10px;
    position: absolute;
    color: grey;
    width: 20vw;
}
@media screen and (max-width:1000px) {
    .skillsec{
        display: flex;
        flex-direction: row;
        height: auto;
        padding: 0;
        padding-top: 100px;
    }
    .tools{
        margin-top: 15vh;
        padding: 0;
        height: auto;
        display: grid;
        margin-left: 5vw;
        grid-template-columns: repeat(1,auto);
        justify-content: center;
        gap: 140px;
        padding-right: 20px;
    }
    .toolhead{
        width:100%;
        overflow:visible;
        padding: 0;
        text-align:right;
        height: auto;
        height: auto;
        margin-top: -40px;
        margin-right: 100px;
    }
    .skillhead{
        padding-right: 20px;
    }
    .skillheadp{
        margin-left: -200px;
        text-align: left;
        width:280px;
    }
    
    .tools{
        justify-content: end;
        margin-top: 20vh;
    }
    .skillele{
        width: 70vw;
        margin-left: -60px;
        padding-right: 10px;
        display: grid;
        grid-template-columns: repeat(4,auto);
    }
    .skicon{
        margin-right: 0;
    }
}


@media screen and (max-width:400px) {
    .skillele{
        grid-template-columns: repeat(3, auto);
        gap: 0;
    }
    .tooltop, .codetop, .skilltop{
        padding-left:40px;
        padding-right: 20px;
    }
}