.card1{
    background-color: #31425691;
    border-radius: 10px;
    height: 350px;
    padding: 10px;
}
.card{
    width: 300px;
    color: rgb(175, 167, 167);
    height: 350px;
    align-items: center;
    justify-content: center;
}
.cardimg{
    width: 100%;
    border-radius: 10px;
    height: 45%;
}
.projectsec{
    display: grid;
    grid-template-columns: repeat(1,auto);
    gap: 30vh;
    margin-top: 20vh;
}
.project{
    display: grid;
    grid-template-columns: repeat(2,auto);
    justify-content:right;
    color: white;
    gap: 10vw;
    padding-right: 10vw;
}
.procon{
    max-width: 40vw;
}
.prohead{
    background-color: red;
    position: absolute;
    margin-top: 120vh;
    margin-left: 40px;
}
.explore{
    color: white;
    text-align: end;
    padding-right: 170px;
    padding-top:80px;
    font-size: 20px;
    a{
        text-decoration: none;
    }
    a:hover{
        color: rgb(38, 164, 218);
    }
}
.altgithub{
    position: absolute;
    margin-top: -210px;
    margin-left: 230px;
    justify-content: center;
    display: none;
}
.reppro{
    display: none;
}
.pronam{
    color: white;
    font-size: 80px;
}
.prosub{
    color: grey;
}
@media screen and (max-width:1150px) {
    .prohead{
       display: none;
    }
}
@media screen and (max-width:1000px) {
    .reppro{
        display: block;
        position: absolute;
        margin-top: 150px;
        margin-left: 7vw;
    }
    .procon{
        display: none;
    }
    .project{
        padding: 0;
    }
    .projectsec{
        display: grid;
        grid-template-columns: repeat(2,auto);
        justify-content:space-evenly;
        gap: 60px;
        padding-left: 8vw;
        margin-top: 350px;
    }
    .altgithub{
        display: block;
    }

}
@media screen and (max-width:800px) {
    .projectsec{
        display: grid;
        grid-template-columns: repeat(1,auto);
        justify-content: center;
        gap: 40px;
    }
}
@media screen and (max-width:500px) {
    .reppro{
        margin-left:10%;
    }
    .explore{
        padding:0px;
        margin-top: 50px;
        padding-right: 60px;
    }
}