.messagesec{
    margin-top: 20vh;
}
.message{
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content: left;
    padding-left: 15vw;
    padding-top: 10vh;
    padding-bottom: 10vh;
    color: white;
}
.input,.textarea{
    padding: 5px;
}
.input{
    background: none;
    border-bottom: 2px solid rgba(255, 255, 255, 0.296);
    border-left: none;
    border-right: none;
    border-top: none;
    color: rgb(255, 255, 255);
    font-size: 18px;
    width: 30vw;
}
::placeholder{
    color: rgb(238, 224, 224);
    font-family: 'Courier New', Courier, monospace;
}
.input:focus,.textarea:focus{
    outline: none;
    border-color: rgb(38, 164, 218);
}
.msg{
    display: grid;
    grid-template-columns: repeat(2,auto);
    padding-top: 10vh;
    color: white;
}
.abt1{
    padding: 10vh;
    align-items: center;
    justify-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 80px;
}
.textarea{
    background: none;
    font-size: 18px;
    width:30vw;
    color: rgba(255, 255, 255, 0.638);
    height: 20vh;
    border-color: rgb(89, 85, 85);
    border-radius: 5px;
}
.sendbut{
    background: rgb(1, 79, 112);
    border: 1px solid rgb(1, 79, 112);
    padding: 5px;
    width: 100px;
    height: 40px;
    color: rgb(255, 255, 255);
    border-radius: 20px;
    float: left;
    margin-left: 22vw;
}
.msg{
    border:1px solid rgba(255, 255, 255, 0.444);
    background-color: #2a364550;
    padding: 0;
    margin-top: 80px;
    width: 60vw;
    border-radius: 10px;
}
.form{ 
    padding: 2vw;
}
.msgicons{
    font-size: 18px;
    padding-top: 10px;
    display: grid;
    grid-template-columns: repeat(5,auto);
    justify-content: left;
    gap: 20px;
    a:hover{
        color: rgb(38, 164, 218);
    }
}
.name{
    font-size: 10px;
}
.mydetail{
    padding: 2vw;
}
.toast{
    background-color: red;
    position:fixed;
    color: rgb(246, 246, 246);
}

@media screen and (max-width:1000px) {
    .text{
        display: none;
    }
    .msg{
        display: grid;
        grid-template-columns: repeat(1,auto);
        width: 80vw;
        margin-top: 180px;
        padding: 10px;
    }
    .form{
        .input{
            width: 95%;
        }
        .textarea{
            width: 95%;
        }
        .sendbut{
            margin-left: 60vw;
        }
    }
    .message{
        padding: 0;
        justify-content: center;
    }
}
@media screen and (max-width:500px) {
    .form{
        width: auto;
        overflow-x: hidden;
        .sendbut{
            margin-left:54vw;
        }
    }
}
@media screen and (max-width:400px) {
    .form{
        .sendbut{
            margin-left: 46vw;
        }
    }
}