.about{
    height: 100vh;
    margin-top: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: repeat(2,auto);
    justify-content: center;
    color: white;
}
.abt{
    padding: 10vh;
    padding-top: 30vh;
    align-items: center;
    justify-items: center;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 80px;
}
.abtcnt{
    width: 40vw;
    color: rgb(220, 207, 207);
    font-size: 18px;
    padding-top: 150px;
    padding-left: 10vh;
    padding-right: 10vh;
}
.contacts{
    height: auto;
    display: grid;
    grid-template-columns: repeat(3,auto);
    justify-content: left;
    padding-top: 30px;
    gap: 30px;
}
#conbut1,#conbut2,#conbut3{
    font-size: 20px;
    padding: 1;
}
.conicon{
    padding: 0;
    margin-top: 5px;
}
.sidenav{
    color: white;
    position: absolute;
    margin-top: 100vh;
    height: 100vh;
    width: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-family:'Courier New', Courier, monospace;
    margin-left: 10px;
    a{
        text-decoration: none;
    }
}

@media screen and (max-width:1000px) {
    .sidenav{
        display: none;
    }
    .about{
        display: grid;
        grid-template-columns: repeat(1,auto);
        justify-content: left;
        gap: 0;
        height: auto;
        padding: 0;
        margin-top: 60vh;
    }
    .abt{
        margin-left: -1vw;
    }
    .abtcnt{
        width:90%;
        padding: 0;
        padding-left: 7vw;
        padding-right: 2vw;
        margin-top: -50px;
    }
}
@media screen and (max-width:800px) {
    .abt{
        margin-left: -30px;
    }
}
@media screen and (max-width:520px) {
    .abt{
        margin-left: -15vw;
    }
}
