.logo{
    z-index: 2;
    margin-top: 2.3vh;
    position: absolute;
    margin-left: 4vw;
    font-size: 24px;
    color: rgb(255, 255, 255);
    cursor: pointer;
    display: flex;
    gap: 10px;
}  
.landsec{
    display: grid;
    grid-template-columns: repeat(3,auto);
    justify-content: center;
    width: 100vw;
    height: 100vh;
    align-items: center;
}
.icons1{
    width: 20vw;
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content:right;
    padding-right: 20px;
    gap: 50px;
}
.profile{
    width: 20vw;
    img{
        width: 100%;
    }
}
.icons2{
    width: 20vw;
    display: grid;
    grid-template-columns: repeat(1,auto);
    justify-content:left;
    padding-left: 20px;
    gap: 50px;
    padding-top:45px;
    margin-top: -40px;
}
.button {
    width: 60px;
    padding-top: 15px;
    padding-bottom: 15px;
    text-align: center;
    font-size: 22px;
    border-radius: 50%;
    background: #e8e8f3;
    bottom: 0;
    right: 0;
    box-shadow: inherit;
    z-index: 10;
    color: #1b2735;
    position: relative;
    overflow: hidden;
    border: 1px solid rgb(132, 128, 128);
    color: #1b2735;
  }
  .button::after{
    content: '';
    width: 60px;
    height: 60px;
    top: -60px;
    left: 0;
    z-index: -1;
    background-color: #1b2735;
    position: absolute;
    transition: 0.5s;
  }
  .button:hover::after{
    top: 0;
  }
  .button:hover{
    color: white;
  }
  #navbut1{
    margin-left: 100px;
  }
  #navbut2{
    margin-left: 40px;
  }
  #navbut3{
    margin-left: 40px;
  }
  #navbut4{
    margin-left: 100px;
  }
  #navbut5{
    margin-left: 40px;
  }
  #navbut6{
    margin-left: -10px;
  }
  .scroll-down {
    height: 35px;
    width: 18px;
    border: 2px solid #ffffff;
    position: absolute;
    left: 50%;
    bottom: 20px;
    border-radius: 50px;
    cursor: pointer;
  }
  .scroll-down::after {
    content: "";
    position: absolute;
    top: 10%;
    left: 18%;
    height: 10px;
    width: 10px;
    border-radius: 50%;
    background-color: white;
    border: 2px solid #ffffff;
    border-top: transparent;
    border-left: transparent;
    animation: scroll-down 1s ease-in-out infinite;
  }
  .scroll-down::before {
    top: 30%;
    animation-delay: 1s;
  }
  .scroll-name{
    color: white;
    position: absolute;
    margin-top: 15px;
    margin-left: -10px;
  }
  .button{
    a{
      color: #1b2735;
    }
  }
  .button:hover{
    a{
      cursor: none;
      color: #e8e8f3;
    }
  }
  a{
    color: white;
    cursor: none;
  }
  
  @keyframes scroll-down {
    0% {
      opacity: 0;
    }
    30% {
      opacity: 1;
    }
    60% {
      opacity: 1;
    }
    100% {
      top: 50%;
      opacity: 0;
    }
  }
  .chat{
    color: white;
    position: absolute;
    right: 0;
    left: auto;
    margin-top: -40px;
    font-size: 20px;
    margin-right: 50px;
  }
  .name{
    font-weight: bold;
    color: lightgrey;
    font-size: 2rem;
  }
  .views{
    color: white;
    position: absolute;
    width: 100%;
    text-align: end;
    margin-left: -50px;
    margin-top: 10px;
  }

  @media screen and (max-width:1000px) {
    .profile{
      width: 40vw;
    }
    .landsec{
      margin-top: -100px;
    }
    .chat{
      margin-top: -105px;
    }
  }
  @media screen and (max-width:600px) {
    .profile{
      width: 50vw;
    }
    .icons2{
      margin-top: -50px;
      padding: 0;
      padding-left: 10vw;
      span{
        margin-top: -100px;
      }
    }
    .icons1,#navbut5, #navbut6{
      display: none;
    }
    .pro-name{
      margin-left: -40px;
      font-size: 1rem;
    }
  }
  @media screen and (max-width:500px) {
    .profile{
      width: 60vw;
      margin-left: -20px;
    }
    .pro-name{
      margin-left: -60px;
      font-size: 15px;
    }
  }