.cursor-dot,
.cursor-dot-outline {
  pointer-events: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  opacity: 1;
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  z-index: 100;
}

.cursor-dot {
  width: 8px;
  height: 8px;
  background-color: #ffffff;
}

.cursor-dot-outline {
  width: 40px;
  height: 40px;
  border: 1px solid white;
}
html{
    cursor: none;
}
@media screen and (max-width:600px) {
  .cursor-dot,
.cursor-dot-outline{
  display: none;
}
}