.scoicons{
    display: grid;
    grid-template-columns: repeat(1,auto);
    position: absolute;
    margin-top: -140px;
    padding-left:10px;
    gap: 10px;
}
.socicon{
    padding-top: 5px;
}
.follow{
    position: absolute;
    width: 200px;
}
.line::after{
    content: ' ';
    background-color: white;
    position: absolute;
    width: 50px;
    height: 2px;
    margin-top: 11px;
    margin-left: 10px;
}
.flw{
    margin-left: 70px;
    color: white;
}
.text{
    transform: rotate(270deg);
    position: absolute;
    margin-top: -120px;
    padding-left: 30px;
}
.contact{
    background-color: red;
    position: absolute;
    margin-left: 95vw;
    margin-top: -37vh;
}

@media screen and (max-width:1000px) {
    .contact{
        display: none;
    }
    .socialcon{
        margin-top: -70px;
    }
}