.preloader {
    height: 100vh;
    width: 100%;
    background: #1b2735;
    color:'white';
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    z-index: 1;
}

.texts-container {
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 80px;
    width: 40vw;
    font-size: 30px;
    overflow: hidden;
    font-weight: 800;
    color: white;
    position: absolute;
    z-index: 2;
    margin-top: -20px;
}

.texts-container span {
    display: inline-block;
    opacity: 0;
    margin-left: 10px;
    animation: fadeIn 3s forwards;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    25% {
        opacity: 1;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

.texts-container span:nth-child(1) {
    animation-delay: 0s;
}

.texts-container span:nth-child(2) {
    animation-delay: 1s;
}

.texts-container span:nth-child(3) {
    animation-delay: 2s;
}

@media (max-width: 768px) {
    .preloader{
        justify-content: center;
    }
    .texts-container{
        font-size: 5vw;
        padding: 0;
        width:100%;
        padding-right: 40px;
    }
}
